import Vue from "vue";
import VueRouter from "vue-router";
import Index from "@/components/Index.vue";
import FingerCulture from "@/components/FingerCulture.vue";

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: "/",
      name: "Index",
      component: Index
    },
    {
      path: "/fingerculture",
      name: "FingerCulture",
      component: FingerCulture
    }
  ],
  scrollBehavior() {
    return { x: 0, y: 0 };
  }
});
